@use '../abstracts';
.nav {
	display: flex;
	align-items: center;
    justify-content: space-between;
}
.logo__wrapper {
	flex: 1 1 25%;
}
.navigate {
	display: flex;
	align-items: end;
	flex: 1 1 75%;
	justify-content: end;
	gap: 1rem;
}
.nav__link--btn {
	color: abstracts.$white;
    text-decoration: none;
    font-size: abstracts.calculateRem(12);
}